
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  

.car {
    border: 5px;
    border-radius: 10px;
    display: flex;
    font-display: flex;
    overflow:visible;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100vh;
    width: 100vw;

    margin: 0 auto;
    text-align: center;
    display: flex;
    align-items:center;
    justify-content:center;
    background-image: url("weather.jpg");
    background-size: cover;
    background-position: 100%;
    opacity: 0.9;
  }
  .card-bo{
    grid-row: auto;
    padding: 5px;
    font-size: 2rem;
    font-weight: 100;
    display: flex;
    font-display: flex;
    overflow: hidden;
    flex-direction: row;
    overflow: hidden;
    align-items: center;
    justify-content: center;
    letter-spacing: 1px;
    display: grid;
    grid-template-rows: repeat(1,1fr);
    grid-gap: 30px;
    border: 3px solid blueviolet;
    border-radius: 15px;
    background-color: rgb(185, 187, 205);
margin-left: 30px;
margin-right: 30px;
margin-top: 10px;
margin-bottom: 10px;

}
h4{
    font-size: 40px;
}
  input[type="submit"] {
    background-color: lightblue;
    border-radius: 10px;
    padding: 5px;
  }
  input[type="text"] {
    border-radius: 5px;
    padding: 5px;
    size: 50px;

    
  }
  .res{
    font-size: medium;
    font-weight: 300;
    font-style: italic;


  }